<template lang="pug">
  div(class="beginner-guide-root")
    div(v-if="currentScenes === 0" @click="nextScenes" :style="{'background-image':`url(${require('@/assets/image/beginnerGuide/story-00.png')})`}")
      p 西元二十五世紀，<br>地球上的環境被人類破壞殆盡，<br>自然能源所剩無幾
    div(v-if="currentScenes === 1" @click="nextScenes" :style="{'background-image':`url(${require('@/assets/image/beginnerGuide/story-01.png')})`}")
      p 為了持續發展科技，<br>科學家絞盡腦汁解決能源不足的問題
    div(v-if="currentScenes === 2" @click="nextScenes" :style="{'background-image':`url(${require('@/assets/image/beginnerGuide/story-02.png')})`}")
      p 最終，<br>科學家們研發出了新的黑色神秘物質<br>---- 波波拉
    div(v-if="currentScenes === 3" @click="nextScenes" :style="{'background-image':`url(${require('@/assets/image/beginnerGuide/story-03.png')})`}")
      p 波波拉可以聚集空間中些微物質，<br>匯聚成能量
    div(v-if="currentScenes === 4" @click="nextScenes" :style="{'background-image':`url(${require('@/assets/image/beginnerGuide/story-04.png')})`}")
      p 各地開始大規模使用波波拉來吸收能源，<br>以開發先進科技
    div(v-if="currentScenes === 5" @click="nextScenes" :style="{'background-image':`url(${require('@/assets/image/beginnerGuide/story-05.png')})`}")
      p 然而在大眾沈浸於波波拉所帶來的美好時，<br>波波拉的恐怖副作用卻在此時展現，<br>吸取過多能量的波波拉，<br>人們漸漸無法控制它的能量
    div(v-if="currentScenes === 6" @click="nextScenes" :style="{'background-image':`url(${require('@/assets/image/beginnerGuide/story-05.png')})`}")
      p 波波拉大規模擴散於地表當中，<br>許多人類被波波拉吸收而莫名的失蹤
    div(v-if="currentScenes === 7" @click="nextScenes" :style="{'background-image':`url(${require('@/assets/image/beginnerGuide/story-07.png')})`}")
      p(:style="{'color': '#EEEEEE'}") 驚慌的人類紛紛駕駛著火箭，<br>逃離被波波拉侵蝕的地球，<br>尋找適合居住的星球
    div(v-if="currentScenes === 8" @click="nextScenes" :style="{'background-image':`url(${require('@/assets/image/beginnerGuide/story-08.png')})`}")
      p(:style="{'color': '#EEEEEE', 'padding-top': '116px'}") 幸運的是在離地球不遠處，<br>有幾個適合居住的星球，<br>讓我們挑選其一並前往重建家園吧！
    div(v-if="currentScenes === 9" :style="{'background-image':`url(${require('@/assets/image/beginnerGuide/story-09.png')})`}")
      p(:style="{'color': '#EEEEEE'}") 點擊畫面下方拉桿可變更星球
      div
        img(:src="planetInfo[currentPlanet].image" :style="{'width': '200px'}")
      div(class="planet-info")
        h6 {{planetInfo[currentPlanet].title}}
        div
          p {{planetInfo[currentPlanet].description}}
          div
            span 特殊礦產資源： <em>{{planetInfo[currentPlanet].specialMineral}}</em>
            span 加成與劣勢
            template(v-for="value, content in planetInfo[currentPlanet].prosAndCons")
              div
                span {{content}}
                span {{value}}%
            span(@click="isSelect = true") <b>選擇此星球</b>
        div(class="remote-control" @click="switchPlanet")
        div(v-if="isSelect" class="modal-card")
          div
            h5 請為您的星球取名
            input(type="text" class="input-default" placeholder="星球名稱" v-model="planetName")
            div
              span(@click="isSelect = false") 取消
              span(@click="confirm(planetInfo[currentPlanet].type)") 確定
</template>

<script>
import { PlanetGameFunc } from '@/game/scenes/PlanetScene'
import { mapState, mapActions, mapGetters } from 'vuex'

export default {
  data () {
    return {
      currentScenes: 0,
      currentPlanet: 0,
      planetInfo: [
        {
          title: '奎奈爾星體',
          type: '奎奈爾',
          description: '奎奈爾星體奎奈爾星體奎奈 奎奈爾星體奎奈爾星體奎奈 奎奈爾星體奎奈爾星體奎奈 奎奈爾星體奎奈爾星體奎奈 奎奈爾星體奎奈爾星體奎奈 奎奈爾星體奎奈爾星體奎奈',
          image: require('@/assets/image/planet/planet-2.png'),
          specialMineral: '鍨金屬',
          prosAndCons: {
            '建造時間': -5,
            '軍事訓練時間': -5,
            '農業發展能力': -5
          }
        },
        {
          title: '赫克斯星體',
          type: '赫克斯',
          description: '奎奈爾星體奎奈爾星體奎奈 奎奈爾星體奎奈爾星體奎奈 奎奈爾星體奎奈爾星體奎奈 奎奈爾星體奎奈爾星體奎奈 奎奈爾星體奎奈爾星體奎奈 奎奈爾星體奎奈爾星體奎奈',
          image: require('@/assets/image/planet/planet-1.png'),
          specialMineral: '氪金屬',
          prosAndCons: {
            '建造時間': -5,
            '軍事訓練時間': -5,
            '農業發展能力': -5
          }
        }
      ],
      isSelect: false,
      planetName: ''
    }
  },
  methods: {
    ...mapActions('user', ['completeBeginningStory']),
    ...mapActions('planet', ['AddPlanet']),
    nextScenes () {
      this.currentScenes += 1
    },
    switchPlanet () {
      this.currentPlanet += 1
      if (this.currentPlanet === this.planetInfo.length) {
        this.currentPlanet = 0
      }
    },
    async confirm (type) {
      await this.AddPlanet({ token: this.token, type, name: this.planetName })
      await this.completeBeginningStory()
      this.lauchBootScene()
      this.$router.push('/game')
    },
    lauchBootScene () {
      PlanetGameFunc.ChangeScene()
      import('@/game/scenes/PlayScene').then(async (module) => {
        module.getBuildingMap(this.planet.scale, this.planet.buildingMap)
      })
    }
  },
  computed: {
    ...mapGetters('user', {
      token: 'getToken'
    }),
    ...mapState('planet', {
      planet: state => state.planet
    })
  }
}
</script>

<style lang="scss" scoped>
@import "@/assets/scss/variables.scss";

.beginner-guide-root {
  position: fixed;
  width: 100%;
  height: 100%;
  > div {
    width: 100%;
    height: 100%;
    background-position: center;
    > p {
      font-size: 14px;
      line-height: 24px;
      color: #000000;
      padding-top: 40px;
    }
    .planet-info {
      position: relative;
      padding: 92px 23px 0 23px;
      > h6 {
        font-size: 16px;
        line-height: 16px;
        padding-bottom: 8px;
      }
      > div:nth-child(2) {
        display: grid;
        grid-template-columns: 144px 154px;
        grid-column-gap: 30px;
        > p {
          font-size: 12px;
          line-height: 24px;
        }
        > div {
          display: grid;
          grid-auto-rows: 14px;
          grid-row-gap: 12px;
          justify-items: start;
          margin-top: 6px;
          span {
            font-size: 12px;
            line-height: 12px;
            em {
              color: #ff417c;
              font-style: normal;
            }
          }
          > div {
            display: flex;
            justify-content: space-between;
            border-bottom: 1px solid map-get($colors, primary);
            width: 129px;
            margin-left: 24px;
          }
          > span:last-child {
            padding-top: 6px;
            font-size: 14px;
            justify-self: end;
            color: #ff417c;
          }
        }
      }
      .remote-control {
        position: absolute;
        z-index: 10;
        bottom: -53px;
        left: 50%;
        width: 50px;
        height: 50px;
        border-radius: 50%;
        transform: translateX(-52%);
      }
      .modal-card {
        position: fixed;
        z-index: 50;
        top: 0px;
        left: 0px;
        width: 375px;
        height: 667px;
        background-color: rgba($color: #000000, $alpha: 0.7);
        > div {
          position: fixed;
          top: 229px;
          left: 25px;
          width: 324px;
          height: 150px;
          border: 1px solid map-get($colors, white);
          background-color: rgba($color: #000000, $alpha: 0.7);
          border-radius: 7.5px;
          box-shadow: map-get($shadow, secondary-4);
          display: grid;
          grid-auto-columns: 276px;
          grid-template-rows: 22px 14px 21px 36px 22px 14px 1fr;
          grid-template-areas:
            "."
            "title"
            "."
            "input"
            "."
            "button"
            ".";
          justify-content: center;
          > h5 {
            grid-area: title;
            font-size: 14px;
            font-weight: 900;
          }
          > input {
            grid-area: input;
            height: 36px;
          }
          > div {
            grid-area: button;
            justify-self: end;
            display: flex;
            span {
              font-size: 14px;
              font-weight: 900;
              margin-right: 11px;
              &:nth-child(2) {
                color: #ff417c;
              }
            }
          }
        }
      }
    }
  }
}
</style>
